import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory, generatePath } from 'react-router';
import { TUser } from '../../../types';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import { PATHS } from '../../../routes/path';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

type Props = {
	users: Array<TUser>;
	isLoading: boolean;
	hasMore?: boolean;
	loadMore?: Function;
};

const UsersTable = ({ users, isLoading, hasMore, loadMore }: Props) => {
	const classes = useStyles();
	const history = useHistory();

	const editUser = (id: string) => {
		const userEditPath = generatePath(PATHS.USERS_EDIT, { id });
		history.push(userEditPath);
	};

	if (_isEmpty(users) && !isLoading) {
		return (
			<Grid item xs={12}>
				<Alert icon={false} severity='info'>
					No Records found
				</Alert>
			</Grid>
		);
	}

	return (
		<>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header />
					<TableBody>
						{users.map(user => {
							if (typeof user === 'number') return null;

							return (
								<TableRow hover tabIndex={-1} key={user.UserId}>
									<TableCell component='th'>{_get(user, 'FirstName', '')}</TableCell>
									<TableCell align='left'>{_get(user, 'LastName', '')}</TableCell>
									<TableCell align='left'>{_get(user, 'Email', '')}</TableCell>
									<TableCell align='left'>{_get(user, 'Phone', '')}</TableCell>
									<TableCell align='left'>
										{_get(user, ['UserGroups', '0', 'Description'], '')}
									</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton color='primary' onClick={() => editUser(user.UserId)}>
											<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>

			{hasMore && loadMore && (
				<LoadMore onLoadMore={loadMore} loading={isLoading} />
			)}
		</>
	);
};

export default UsersTable;
