import { useState } from 'react';
import { alpha, makeStyles } from '@material-ui/core/styles';
import { Toolbar, Tooltip, IconButton, Box, Grid } from '@material-ui/core';
import FilterListIcon from '@material-ui/icons/FilterListTwoTone';
import SearchFilter from './components/SearchFilter';
import type { Props } from './components/SearchFilter';

const useToolbarStyles = makeStyles(theme => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
	},
	highlight: {
		backgroundColor: alpha(
			theme.palette.primary.main,
			theme.palette.action.activatedOpacity
		),
	},
	title: {
		flex: '1 1 100%',
	},
}));

const UserToolbar = (props: Props) => {
	const classes = useToolbarStyles();
	const [openFilter, setOpenFilter] = useState(false);

	const toggleFilter = () => setOpenFilter(!openFilter);

	return (
		<>
			<Toolbar className={classes.root}>
				<SearchFilter openFilter={openFilter} {...props} />
			</Toolbar>
			<Grid container alignItems='center' justifyContent='flex-end'>
				<Box sx={{ display: { xs: 'block', lg: 'none' } }}>
					<Tooltip title='Filter'>
						<IconButton
							sx={{
								width: '48px',
								height: '48px',
							}}
							onClick={toggleFilter}
						>
							<FilterListIcon />
						</IconButton>
					</Tooltip>
				</Box>
			</Grid>
		</>
	);
};

export default UserToolbar;
