import { useEffect, useState } from 'react';
import _get from 'lodash.get';
import { useQuery, useLazyQuery, gql } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import { TUser } from '../../../types';
import graphql from '../../../graphql';
import Loader from '../../../ui-component/Loader';
import config from '../../../config';
import UserToolbar from './UserToolbar';
import { useSnackBar } from '../../../hooks';
import UsersTable from './UsersTable';
import { USER_STATUS } from './config';

const UsersList = () => {
	const showSnackBar = useSnackBar();

	const { loading, error, data, fetchMore } = useQuery(gql(graphql.getUsers), {
		variables: { PageIndex: 0, PageSize: config.pagination.limit },
		fetchPolicy: 'cache-first',
		notifyOnNetworkStatusChange: true,
	});

	const [doSearch, setDoSearch] = useState(false);
	const [search, setSearch] = useState('');
	const [selectedStatus, setSelectedStatus] = useState('');
	const [selectedGroup, setSelectedGroup] = useState('');
	const [executeSearch, { loading: isSearching, data: searchData }] =
		useLazyQuery(gql(graphql.getUsers));
	const {
		loading: loadingUserGroups,
		error: userGroupsError,
		data: userGroupsData,
	} = useQuery(gql(graphql.users_UserGroups));

	const groups = _get(userGroupsData, 'users_UserGroups');
	const [rows, setRows] = useState<TUser[]>([]);
	const [hasMore, setHasMore] = useState<boolean>(false);
	const [page, setPage] = useState<number>(0);

	useEffect(() => {
		const newData = doSearch ? searchData : data;
		const newCount = _get(newData, 'users_GetUserProfiles.Count', 0);
		const newTotal = _get(newData, 'users_GetUserProfiles.Total', 0);
		setHasMore(newCount < newTotal);
		setPage(_get(newData, 'users_GetUserProfiles.PageIndex') || 0);
		setRows(_get(newData, 'users_GetUserProfiles.Items') || []);
	}, [data, searchData, doSearch]);

	const isLoading = loading || isSearching || loadingUserGroups;
	const isError = error || userGroupsError;
	if (isLoading && !data) return <Loader />;

	if (isError) {
		showSnackBar({
			message:
				_get(error, 'message') ||
				_get(userGroupsError, 'message', 'Something went wrong'),
			alertSeverity: 'error',
		});
	}

	const searchHandler = (pageIndex = 0) => {
		executeSearch({
			variables: {
				PageIndex: pageIndex,
				PageSize: config.pagination.limit,
				...(!_isEmpty(search) && { Query: search.trim() }),
				...(selectedStatus !== '' && { Enabled: selectedStatus }),
				...(!_isEmpty(selectedGroup) && { UserGroups: selectedGroup }),
			},
		});
		setDoSearch(true);
	};

	const loadMore = () => {
		if (doSearch) {
			searchHandler(page + 1);
			return;
		}
		fetchMore({
			variables: { PageIndex: page + 1, PageSize: config.pagination.limit },
		});
	};

	return (
		<>
			{isSearching && <Loader />}
			<UserToolbar
				onSearch={searchHandler}
				onTriggerSearch={setDoSearch}
				statuses={USER_STATUS}
				search={search}
				setSearch={setSearch}
				selectedStatus={selectedStatus}
				setSelectedStatus={setSelectedStatus}
				groups={groups}
				selectedGroup={selectedGroup}
				setSelectedGroup={setSelectedGroup}
			/>
			<UsersTable
				users={rows}
				isLoading={isLoading}
				hasMore={hasMore}
				loadMore={loadMore}
			/>
		</>
	);
};

export default UsersList;
