import React from 'react';
import { Button, Grid } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import { makeStyles } from '@material-ui/core/styles';
import SearchField from '../../components/SearchField';
import type { Props as SearchFieldProps } from '../../components/SearchField';
import EnableSelector from '../../components/EnableSelector';
import type { Props as EnableSelectorProps } from '../../components/EnableSelector';
import UserGroupSelector from '../../components/UserGroupSelector';
import type { Props as UserGroupSelectorProps } from '../../components/UserGroupSelector';

export type Props = Omit<SearchFieldProps, 'handleReset'> &
	EnableSelectorProps &
	UserGroupSelectorProps & {
		onSearch: Function;
		onTriggerSearch: Function;
		openFilter?: Boolean;
	};

const useStyles = makeStyles(() => ({
	form: {
		width: '100%',
	},
}));

const SearchFilter = ({
	statuses,
	onSearch,
	onTriggerSearch,
	search,
	setSearch,
	selectedStatus,
	setSelectedStatus,
	openFilter = true,
	groups,
	selectedGroup,
	setSelectedGroup,
}: Props) => {
	const classes = useStyles();

	const onSubmit = (e: React.SyntheticEvent) => {
		e.preventDefault();
		onSearch();
	};

	const filterStyleProps = {
		display: { xs: openFilter ? 'block' : 'none', lg: 'block' },
	};

	return (
		<form onSubmit={onSubmit} className={classes.form}>
			<Grid
				container
				direction='row'
				justifyContent='flex-start'
				alignItems='center'
				spacing={1}
			>
				<Grid item xs={12} lg={3}>
					<SearchField search={search} setSearch={setSearch} />
				</Grid>
				<Grid item xs={12} lg={3} sx={filterStyleProps}>
					<EnableSelector
						selectedStatus={selectedStatus}
						setSelectedStatus={setSelectedStatus}
						statuses={statuses}
					/>
				</Grid>
				<Grid item xs={12} lg={3} sx={filterStyleProps}>
					{!_isEmpty(groups) && (
						<UserGroupSelector
							groups={groups}
							selectedGroup={selectedGroup}
							setSelectedGroup={setSelectedGroup}
						/>
					)}
				</Grid>
				<Grid item xs={12} lg={1} sx={filterStyleProps}>
					<Button
						type='submit'
						variant='contained'
						color='primary'
						size='large'
						fullWidth
					>
						Search
					</Button>
				</Grid>
				<Grid item xs={12} lg={1} sx={filterStyleProps}>
					<Button
						type='button'
						variant='outlined'
						size='large'
						fullWidth
						onClick={() => {
							setSearch('');
							setSelectedStatus('');
							onTriggerSearch(false);
						}}
					>
						Reset
					</Button>
				</Grid>
			</Grid>
		</form>
	);
};

export default SearchFilter;
